import React from 'react';
import './cardList.css'
import { CardItem } from '../cardItem/CardItem.jsx'
 

export const CardList = (props)=>{
    return (
        <div className='cardList'>
        {props.monsters.map((monster, index) => (
            <CardItem key={index} monster={monster} id={index}/>
        
        ))}
        </div>
    )
};